import Select from "react-select";
import { Controller, useForm } from "react-hook-form";

import Button from "components/Button";
import LabelInput from "components/LabelInput";
import { TextInput } from "components/TextInput";

import { addTask } from "shared/store";

function Form() {
  const {
    register,
    handleSubmit,
    control,
    formState: { errors, isDirty },
    reset,
  } = useForm();

  function getData(): any[] | void {
    const source = localStorage.getItem("category");
    if (source) {
      return JSON.parse(source);
    }
    return undefined;
  }

  const onSubmit = async (data: any) => {
    addTask(data);
    reset();
  };
  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <fieldset>
        <LabelInput errors={errors.title}>
          <Controller
            name="category"
            control={control}
            render={({ field }) => (
              <Select
                {...field}
                placeholder=" "
                className="mb-4 w-[240px]"
                options={getData() || []}
              />
            )}
          />
        </LabelInput>
        <LabelInput label="">
          <TextInput
            {...register("title", { required: "   " })}
            placeholder="  "
          />
        </LabelInput>

        <LabelInput label="">
          <TextInput
            {...register("description", { required: "   " })}
            placeholder="  "
          />
        </LabelInput>

        <Button text="" type="submit" className="bg-lazur" disabled={!isDirty} />
      </fieldset>
    </form>
  );
}

export default Form;
